// 
// user.scss
// Use this to write your custom SCSS
//

// Placeholders

%fa {
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    font-family: "Font Awesome 5 Pro";
}


// Color classes

// Custom Grayscale
.light {
    color: $light;
}

.gray-l2 {
    color: $gray-l2;
}

.gray-l1 {
    color: $gray-l1;
}

.gray {
    color: $gray;
}

.gray-d1 {
    color: $gray-d1;
}

.gray-d2 {
    color: $gray-d2;
}

.dark {
    color: $dark;
}



body {
    background: linear-gradient(180deg, #F5F5F5 0, rgba(245, 245, 245, 0) 333px);
    background-color: $white;
}

// Offcanvas padding
body>main.offcanvas-enabled {
    // padding-top: 0 !important;
    padding-bottom: 3.3rem;
}

@include media-breakpoint-up(xl) {
    body>main.offcanvas-enabled {
        // padding-top: 5rem !important;
        padding-bottom: 0;
    }

    // If Top notification is visible and one line of text on all screen sizes
    // .sidebar also influenced - fix it
    // padding-top: 123px;
}

//

// Breadcrumbs 
.breadcrumb {
    font-size: 0.75rem;
    color: $gray-d1;

    .breadcrumb-item::before {
        font-family: 'Font Awesome 5 Pro';
        font-size: 0.81rem;
    }

    .breadcrumb-item+.breadcrumb-item::before {
        margin-top: -0.03rem;
        color: lighten($gray, 27);
        content: "\f054";
    }

    .breadcrumb-item:last-child {
        a {
            color: $gray-l1;
        }
    }

}

// Navbar 
.navbar-tool.dropdown::before {
    width: max-content;
}



// Blog
.gallery-item>img {
    border-radius: $blog-image-radius;
}


// Information Pages

.info-wrapper {
    max-width: $md;
}


// Modal

.modal {

    a {
        // color: $primary;

        &:hover {
            color: $dark;
        }
    }

}


// Misc
.password-toggle-btn .password-toggle-check:checked ~ .password-toggle-indicator::before {
    content: '\f06e';
}

.password-toggle-btn .password-toggle-indicator::before {
    font-family: 'Font Awesome 5 Pro';
    content: '\f070';
}

.gallery-item::after {
    font-family: 'Font Awesome 5 Pro';
    content: '\f055';
}  


// Font Fixed Sizes - Font Size Chipoteka (fsc)

.fsc-1 {
    font-size: 2.5rem;
}

.fsc-2 {
    font-size: 2rem;
}

.fsc-3 {
    font-size: 1.75rem;
}

.fsc-4 {
    font-size: 1.5rem;
}

.fsc-5 {
    font-size: 1.25rem;
}

.fsc-6 {
    font-size: 1rem;
}


// Pagination

.pagination-navigation {


    .pagination {
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
}


